

import utils from '../../assets/js/urlConfig.js'
import Toast from 'vux/src/components/toast/index.vue'

export default {
  components: {
        Toast
    },
    data() {
        return {
          toastTxt: '',
          toastShow: false,
          imgShow:false,
          items:[
            {                //类型：Object  必有字段  备注：无
                "id":"123",                //类型：String  必有字段  备注：地址id
                "person":"张三2",                //类型：String  必有字段  备注：收货人
                "phone":"18888888888",                //类型：String  必有字段  备注：收获电话号码
                "address":"陕西西安",                //类型：String  必有字段  备注：收货地址
                "imgShow":false
            },
            {                //类型：Object  必有字段  备注：无
                "id":"123",                //类型：String  必有字段  备注：地址id
                "person":"张三1",                //类型：String  必有字段  备注：收货人
                "phone":"18888888888",                //类型：String  必有字段  备注：收获电话号码
                "address":"陕西西安",                //类型：String  必有字段  备注：收货地址
                  "imgShow":false
            }
          ],
        }
    },
    created(){
      document.title= "我的地址"
    },
    methods:{
      imgShowClick(item){
        console.log(item)
        for(var i in this.items){
          this.items[i].imgShow = false
        }
        item.imgShow = true
        let datas = {
          "openId":utils.LocalStorage.getStore('openId'),
          "id":item.address,
          "person":item.person,
          "phone":item.phone,
          "address":item.address

        }
        this.$http.post(utils.UrlConfig.selAddress,datas)
                      .then(res => {
                        var res = res.data
                          if(res.code=='1'){// 会员
                            this.toastShow = true
                            this.toastTxt = "选择成功"
                          }else{ // 异常
                            this.toastShow = true
                            this.toastTxt = res.msg
                          }
                      }).catch(error => {
                          console.log(error)
                      })
      }
    }

}




