
import utils from '../../assets/js/urlConfig.js'

import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
    components: {
        Group,
        XInput,
        XButton,
        Toast
    },
    data() {
        return {
          toastTxt: '',
          toastShow: false,
          inviteCode:null,
          num1:null,
          num2:null,
          num3:null,
          num4:null,
          num5:null,
          num6:null,
          num7:null,
          num8:null,
          num9:null
        }
    },
    created(){
      document.title="加入会员"
    },
    mounted(){
    },
    methods:{
      inviteCode(){
        this.inviteCode = this.num1 + this.num2 + this.num3 + this.num4 + this.num5 + this.num6 + this.num7 + this.num8 + this.num9
        let datas = {
          "openId":utils.LocalStorage.getStore('openId'),
          "inviteCode":this.inviteCode
        }
        this.$http.post(utils.UrlConfig.inviteCode,datas)
                      .then(res => {
                        var res = res.data
                          if(res.code=='1'){// 会员
                            this.toastShow = true
                            this.toastTxt = res.msg
                          }else{ // 异常
                            this.toastShow = true
                            this.toastTxt = res.msg
                          }
                      }).catch(error => {
                          console.log(error)
                      })
      }
    }
}




