

import Swiper from 'vux/src/components/swiper/swiper.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Group from 'vux/src/components/group/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'




const baseList = [{
    url: 'javascript:',
    img: 'https://static.vux.li/demo/1.jpg',
    title: '送你一朵fua'
}, {
    url: 'javascript:',
    img: 'https://static.vux.li/demo/2.jpg',
    title: '送你一辆车'
}, {
    url: 'javascript:',
    img: 'https://static.vux.li/demo/3.jpg',
    title: '送你一次旅行'
}]
export default {
    directives: {
        TransferDom
    },
    components: {
        Swiper,
        XDialog,
        XButton,
        Flexbox,
        FlexboxItem,
        Group,
        XNumber
    },
    data() {
        return {
            show: false,
            dialogShow: false,
            myAddress: false,
            demo01_list: baseList,
            demo01_index: 0,
            total: 0
        }
    },
    mounted() {
        this.total = 1 * 158

    },
    methods: {
        demo01_onIndexChange(index) {
                this.demo01_index = index
            },
            // 新增地址
            addAddress() {
                this.dialogShow = true

            },
            change(val) {
                console.log('change', val)
                this.total = parseInt(val) * 158
            },
            handlerBuyBtn() {
                this.show = true
            },
            handlerCloseBtn(){
              console.log(11)
              this.show = false
            }
    }
}




