
import utils from '../../assets/js/urlConfig.js'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
    components: {
        XHeader,
        XInput, Group,
        XButton,
        PopupPicker,
        Toast
    },
    data() {
        return {
            userName:'',
            phone:'',
            sex:'',
            address:'',
            inviteName:'',
            sexVal:[],
            toastTxt: '',
            toastShow: false,
        }
    },
    mounted(){

    },
    methods:{
      submitBtn(){

      },
      register(){
        var datas = {
          "openId":utils.LocalStorage.getStore('openId'),
          "userName":this.userName,
          "phone":this.phone,
          "sex":this.sex,
          "address":this.address,
          "inviteName":this.inviteName
        }
        this.$http.post(utils.UrlConfig.register,datas)
                      .then(res => {
                        var res = res.data
                          if(res.code=='1'){
                            this.toastShow = true
                            this.toastTxt = "申请成功"
                          }else{ // 异常
                            this.toastShow = true
                            this.toastTxt = res.msg
                          }

                      }).catch(error => {
                          console.log(error)
                      })
      }
    }
}




