
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
  components: {
    XInput,
      Group,
      Cell,
      Toast
  },
  data(){
    return{
      person:'',
      date:'预计发货时间：2017-12-10',
      mobile:'',
      area:'',
      address:''
    }
  },
  created(){
    document.title="确认提货"
  },
  methods:{
    takeGood(){
      var datas = {
          "openId":utils.LocalStorage.getStore('openId'),
          "takeCode":this.takeCode,
          "address":this.address,
          "person":this.person,
          "phone":this.phone,
          "goodId":this.goodId,
          "reserveMember":this.reserveMember
      }
      this.$http.post(utils.UrlConfig.takeGood,datas)
                    .then(res => {
                      var res = res.data
                        if(res.code=='1'){// 成功
                            this.items = res.list
                        }else{ // 异常
                          this.toastShow = true
                          this.toastTxt = res.msg
                        }

                    }).catch(error => {
                        console.log(error)
                    })
    }
  }
}




