
import utils from '../../assets/js/urlConfig.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
    components: {
        Group, Cell,Toast
    },
    data() {
        return {
            items: [],
            toastTxt: '',
            toastShow: false,
            page:'1'
        }
    },
    created() {
        document.title = "我的订单"
    },
    mounted() {

    },
    methods:{
      myorder(){
        var datas = {
            "openId":utils.LocalStorage.getStore('openId'),
            "page":this.page
        }
        this.$http.post(utils.UrlConfig.myorder,datas)
                      .then(res => {
                        var res = res.data
                          if(res.code=='1'){// 成功
                              this.items = res.list
                          }else{ // 异常
                            this.toastShow = true
                            this.toastTxt = res.msg
                          }

                      }).catch(error => {
                          console.log(error)
                      })
      }
    }
}




